import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { validationMutasiBarang } from "../../Validations/validationMutasiBarang";
import { getTimbangan } from "../../GetTimbangan";
import { getListBarang } from "../../actions/kodeBarangAction";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormMutasiBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listBarang: [],
      kode_asal: "",
      nama_asal: "",
      kadar_asal: "",
      pkg_asal: "",
      kotor_asal: "",
      harga_asal: "",
      murni_asal: "",
      kategori_asal: "",
      kode_tujuan: "",
      nama_tujuan: "",
      kadar_tujuan: "",
      pkg_tujuan: "",
      kotor_tujuan: "",
      harga_tujuan: "",
      murni_tujuan: "",
      kategori_tujuan: "",
    };
  }

  componentDidMount() {
    this.props.dispatch(getListBarang());
    getTimbangan(this.props.change);
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };

  handleChange = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      kadarCetak: result_explode[1],
      kadarBeli: result_explode[2],
      namaJenis: result_explode[3],
      kategori: result_explode[4],
      kadar_jual: result_explode[5],
    });
    this.props.change("kadar", result_explode[1]);
    this.props.change("kadarTransaksi", result_explode[5]);
    this.props.change("keterangan", result_explode[3]);
    this.props.change("kategori", result_explode[4]);
    this.props.change("kadar_jual", result_explode[5]);
    this.props.change("kadar_beli", result_explode[2]);
  };
  handleChangeTujuan = (input) => (e) => {
    var supp = e.target.value
      ? e.target.value
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenisTujuan: result_explode[0],
      kadarCetakTujuan: result_explode[1],
      kadarBeliTujuan: result_explode[2],
      namaJenisTujuan: result_explode[3],
      kategoriTujuan: result_explode[4],
      kadar_jualTujuan: result_explode[5],
    });
    this.props.change("kadarTujuan", result_explode[1]);
    this.props.change("kadarTransaksiTujuan", result_explode[5]);
    this.props.change("keteranganTujuan", result_explode[3]);
    this.props.change("kategoriTujuan", result_explode[4]);
    this.props.change("kadar_jualTujuan", result_explode[5]);
    this.props.change("kadar_beliTujuan", result_explode[2]);
  };
  setKadar() {
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTujuan", this.state.kadarCetakTujuan);
    this.props.change("kadarTransaksi", this.state.kadar_jual);
    this.props.change("kadarTransaksiTujuan", this.state.kadar_jualTujuan);
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("keteranganTujuan", this.state.namaJenisTujuan);
    this.props.change("kadar_jualTujuan", this.state.kadar_jualTujuan);
    this.props.change("kadar_jual", this.state.kadar_jual);
    this.props.change("kadar_beliTujuan", this.state.kadarBeliTujuan);
    this.props.change("kadar_beli", this.state.kadarBeli);
    this.props.change("kategoriTujuan", this.state.kategoriTujuan);
    this.props.change("kategori", this.state.kategori);
  }
  setNetto = (e) => {
    this.props.change(
      "netto",
      this.props.netto === "NaN" ? 0 : this.props.netto
    );
    this.props.change(
      "nettoTujuan",
      this.props.nettoTujuan === "NaN" ? 0 : this.props.nettoTujuan
    );
  };
  setPkg = (input) => (e) => {
    this.props.change("pkgTujuan", e.target.value);
  };
  setBruto = (input) => (e) => {
    this.props.change("brutoTujuan", e.target.value);
  };
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 form-group">
            <label htmlFor="">Kode Asal</label>
            <Field
              name="kodeBarang"
              component="select"
              className="form-control"
              onChange={this.handleChange()}
              onBlur={() => this.setKadar()}
            >
              <option value="DEFAULT|DEFAULT|DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.props.kodeBarang
                .filter((data) => data.kode_kelompok === "PERHIASAN")
                .map((list) => (
                  <option
                    value={
                      list.kode_jenis +
                      "|" +
                      list.kadar_cetak +
                      "|" +
                      list.kadar_beli +
                      "|" +
                      list.nama_jenis +
                      "|" +
                      list.kode_kategori +
                      "|" +
                      list.kadar_jual
                    }
                    key={list.kode_jenis}
                  >
                    {list.nama_jenis}
                  </option>
                ))}
            </Field>
          </div>
          <div className="col-md-4">
            <Field
              name="keterangan"
              label="Keterangan"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-md-4 d-none">
            <Field
              name="kategori"
              label="Keterangan"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-md-4 col-lg-4"></div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadar"
              label="Kadar (%)"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="pkg"
              label="Pkg (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              onChange={this.setPkg()}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="bruto"
              label="Bruto (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              onChange={this.setBruto()}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadarTransaksi"
              label="Harga (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadar_jual"
              label="Harga jual(Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadar_beli"
              label="Harga beli(Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="netto"
              label="Netto (Gr)"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-12 col-md-12 text-center mt-2 mb-2">
            <div style={{ transform: "rotate(90deg)", fontSize: 24 }}>
              <i className="fa fa-exchange-alt"></i>
            </div>
          </div>
          <div className="col-md-3 form-group">
            <label htmlFor="">Kode Tujuan</label>
            <Field
              name="kodeBarangTujuan"
              component="select"
              className="form-control"
              onChange={this.handleChangeTujuan()}
              onBlur={() => this.setKadar()}
            >
              <option value="DEFAULT|DEFAULT|DEFAULT" key="DEFAULT">
                SILAHKAN PILIH
              </option>
              {this.props.kodeBarang
                .filter((data) => data.kode_kelompok === "PERHIASAN")
                .map((list) => (
                  <option
                    value={
                      list.kode_jenis +
                      "|" +
                      list.kadar_cetak +
                      "|" +
                      list.kadar_beli +
                      "|" +
                      list.nama_jenis +
                      "|" +
                      list.kode_kategori +
                      "|" +
                      list.kadar_jual
                    }
                    key={list.kode_jenis}
                  >
                    {list.nama_jenis}
                  </option>
                ))}
            </Field>
          </div>
          <div className="col-md-4 d-none">
            <Field
              name="kategoriTujuan"
              label="Keterangan"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-md-4">
            <Field
              name="keteranganTujuan"
              label="Keterangan"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-4 col-lg-4"></div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadarTujuan"
              label="Kadar (%)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1 ">
            <Field
              name="kadar_jualTujuan"
              label="Harga jual (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1 ">
            <Field
              name="kadar_beliTujuan"
              label="Harga beli (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="pkgTujuan"
              label="Pkg (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="brutoTujuan"
              label="Bruto (Gr)"
              component={renderField}
              type="text"
              onBlur={() => this.setNetto()}
              readOnly={true}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="kadarTransaksiTujuan"
              label="Harga (Gr)"
              component={renderField}
              type="text"
              onBlur={this.setNetto()}
              onChange={() => this.setNetto()}
            />
          </div>
          <div className="col-md-3 mt-1">
            <Field
              name="nettoTujuan"
              label="Netto (Gr)"
              component={renderField}
              type="text"
              readOnly={true}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" onClick={this.submit}>
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormMutasiBarang = reduxForm({
  form: "FormMutasiBarang",
  enableReinitialize: true,
  validate: validationMutasiBarang,
})(FormMutasiBarang);
const selector = formValueSelector("FormMutasiBarang"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi, kadar_jualTujuan } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "kadar_jualTujuan"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    nettoTujuan: (
      parseFloat(bruto) *
      (parseFloat(kadar_jualTujuan) / 100)
    ).toFixed(3),
    kodeBarang: state.provinsi.kodeBarang,
  };
})(FormMutasiBarang);
