export const validationMutasiBarang = (value) => {
    const errors = {}
    if (!value.kadarTransaksiTujuan) {
        errors.kadarTransaksiTujuan = "Tidak Boleh kosong"
    }
    if (!value.kodeBarangTujuan) {
        errors.kodeBarangTujuan = "Tidak Boleh kosong"
    }
    if (!value.pkg) {
        errors.pkg = "Tidak Boleh kosong"
    }
    if (!value.bruto) {
        errors.bruto = "Tidak Boleh kosong"
    }
    return errors
}